@import url("https://fonts.googleapis.com/css2?family=Wix+Madefor+Display:wght@800&display=swap&family=Work+Sans:wght@400&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --color-primary-dark: 17 24 39;
  --color-primary-light: 75 81 96;
  --color-captions: 255 255 255;
  --background-captions: 0 0 0;
  --header-position: sticky;
  --content-offset: 116px;
  --header-height: 180px;
  --header-mb: -116px;
  --avatar-image-transform: translate3d(0rem, 0, 0) scale(1);
  --avatar-border-transform: translate3d(-0.2222222222222222rem, 0, 0) scale(1.7777777777777777);
  --avatar-border-opacity: 0;
  --header-top: 0px;
  --avatar-top: 0px;
}

html {
  font-family: "Work Sans", sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}
html h1,
html h2,
html h3,
html h4,
html h5,
html h6 {
  font-family: "Wix Madefor Display", serif;
}
html body a {
  transition: color 0.25s ease-out, background-color 0.25s ease-out, border-color 0.25s ease-out, box-shadow 0.25s ease-out;
}
html body p {
  font-size: 18px;
}
html body figure {
  display: flex;
  flex-direction: column;
}
html body figure.kg-card img {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-radius: 12px;
}
html body figure img {
  object-fit: cover;
  width: auto;
  height: auto;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
html body figure figcaption {
  padding: 4px 8px;
  font-family: "Work Sans", serif;
  font-style: italic;
  color: rgb(var(--color-captions)/40%);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: rgb(var(--color-primary-dark)/20%);
}

.kg-width-wide img {
  max-width: 85vw;
}
.kg-width-full img {
  max-width: 100vw;
}

p {
  padding-bottom: 12px;
}